<template>
    <div class="video-list">
        <div v-for="item in videoList"
             v-bind:video="item"
             v-bind:key="item.id"
             class="video-item">
            <video
              class="video"
              controls
              autoplay
              playsinline
              ref="videos"
              :muted="item.muted"
              :id="item.id" />
        </div>
    </div>
</template>

