import Swal from 'sweetalert2'

const swalWithMertButtons = Swal.mixin({
  customClass: {
    confirmButton: 'btn btn__primary',
    cancelButton: 'btn btn__cancel'
  },
  buttonsStyling: false
})

const confirm = (
  icon,
  title,
  text,
  confirmButtonText = '확인',
  showConfirmButton = true
) => {
  return swalWithMertButtons.fire({
    icon,
    title,
    text,
    confirmButtonText,
    showConfirmButton,
    timer: showConfirmButton ? '' : 1500
  })
}
const sweetAlert = {
  noIcon (text, buttonText) {
    return confirm(null, null, text, buttonText)
  },
  error (text, buttonText) {
    return confirm('error', null, text, buttonText)
  },
  question (
    text,
    confirmButtonText,
    cancelButtonText
  ) {
    return swalWithMertButtons.fire({
      title: null,
      text,
      showCancelButton: true,
      confirmButtonText,
      cancelButtonText
    })
  },
  input (text, confirmButtonText) {
    return swalWithMertButtons.fire({
      input: 'text',
      inputAttributes: {
        autocapitalize: 'off'
      },
      text,
      confirmButtonText: confirmButtonText
    })
  }
}
export default sweetAlert
