<template>
  <div class="container">
    <div class="row">
      <div class="search">
        <input v-if="entered" placeholder="회의실 이름" class="search__input" v-model="roomId" maxlength="13" readonly="true">
        <input v-else placeholder="회의실 이름" class="search__input" v-model="roomId" maxlength="13">
      </div>
    </div>
    <div class="row">
      <strong style="color: red">
        * 회의 생성 / 입장
      </strong>
      은 서버 환경에 따라
      <br />
      <strong style="color: red">
        1 - 5초 지연
      </strong>
      될 수 있습니다
    </div>
    <div class="row">
      * 버튼을 연달아 클릭 하면
      <strong style="color: red">
        에러가 발생합니다.
      </strong>
      <br />
      <strong style="color: red">
        한 번씩
      </strong>
      만 눌러주세요 :)
    </div>
    <div class="row">
        <div class="circle">
          <span class="circle__btn">
            <span
              v-if="entered"
              class="pause"
              @click="onLeave"
            >
              <i class="fa-solid fa-stop"></i>
            </span>
            <span
              v-else
              class="play"
              @click="onJoin"
            >
              <i class="fa-solid fa-play" />
            </span>
          </span>
          <span v-if="entered" class="circle__back-1"></span>
          <span v-if="entered" class="circle__back-2"></span>
        </div>
    </div>
    <div class="row">
      <button
        type="button"
        class="btn btn__primary"
        @click="refresh">
        <p>새로고침</p>
      </button>
    </div>
      <div class="row">
        * 화면에 오류가 생길 경우 새로고침 버튼을 눌러주세요 :)
      </div>
      <div class="row">
        <vue-webrtc
          ref="webrtc"
          width="100%"
          :roomId="roomId"
        />
      </div>
    </div>
</template>

<script>

import VueWebrtc from '@/components/vue-webrtc.vue'
import sweetAlert from '@/wrapper/sweet-alert'

export default {
  name: 'demo-component',
  components: { VueWebrtc },
  data () {
    return {
      img: null,
      roomId: '',
      entered: false
    }
  },
  methods: {
    onJoin () {
      if (this.roomId.replace(/ /g, '')) {
        this.$refs.webrtc.join()
        this.entered = true
      } else {
        sweetAlert.error('회의실 이름을 입력하세요', '확인').then(() => {
          this.entered = false
        })
      }
    },
    onLeave () {
      sweetAlert.question('회의를 떠날까요?', '떠나기', '머무르기').then(con => {
        if (con.value) {
          this.$refs.webrtc.leave()
          this.entered = false
        }
      })
    },
    refresh () {
      window.location.reload(true)
    }
  }
}
</script>
